import React from "react";
import LayerB from './LayerB';
import '../styles/LayerA.css'

class LayerA extends React.Component {
    state = {
        showDetails : false
    };
    toggleDetail = () => {
        this.setState({showDetails : !this.state.showDetails});
    };

    render() {
        let data = this.props.data;
        let query = this.props.query;
        let timeStamp = data["_timeStamp"] || 0;
        let timeStamp2 = new Date(timeStamp);
        let rows = data[query].rows || [];

        return (
            <div className={"layer-a "+(this.state.showDetails&&"--showDetail")}>
                <div className={"head"} onClick={this.toggleDetail}>
                    <div> { "ㅁ" } { timeStamp2.toLocaleString() } </div>
                    <div> </div>
                    <div> { rows[0].unitPrice.toLocaleString('en') } </div>
                </div>
                <LayerB rows = {rows || []} />

            </div>
        )

    }

}


export default LayerA;