import React from "react";
import { Line } from 'react-chartjs-2';

class LineChart extends React.Component {

    render() {
        let renderData = this.props.renderData || {};
        let renderArray = Object.keys(renderData);
        let query = this.props.query;
        let a = renderData[0] || {};
        let b = a[query] || [];
        let c = b.rows || [];
        let d = c[0] || {};

        let timeStampArray = renderArray.reduce((acc, cuv, idx) => {
            acc.push(new Date(renderData[cuv]._timeStamp).toLocaleString());
            return acc;
        }, []);

        let quantityArray = renderArray.reduce((acc, cuv, idx) => {
            let a = renderData[cuv] || {};
            let b = a[query] || [];
            let c = b.rows || [];
            let d = c.reduce((acc, cuv, idx) => {
                acc += cuv.count;
                return acc;
            },0);
            acc.push(d);
            return acc;
        },[]);

        let lowPriceArray = renderArray.reduce((acc, cuv, idx) => {
            let a = renderData[cuv] || {};
            let b = a[query] || [];
            let c = b.rows || [];
            let d = c[0] || {};
            let e = d.unitPrice || 0;
            acc.push(e);
            return acc;
        }, []);

        let averagePriceArray = renderArray.reduce((acc, cuv, idx) => {
            let a = renderData[cuv] || {};
            let b = a[query] || [];
            let c = b.rows || [];
            let d = c[0] || {};
            let e = d.averagePrice || 0;
            acc.push(e);
            return acc;
        }, []);

        let data = {
            labels : timeStampArray.reverse(),
            datasets : [
                {
                    label : '최저가',
                    borderColor : 'red',
                    borderWidth: 2,
                    pointRadius : 1,
                    backgroundColor : '#FFF0',
                    data : lowPriceArray.reverse(),
                    yAxisID : 'yAxes-1'
                },
                {
                    label : '평균가',
                    borderColor : 'blue',
                    borderWidth: 2,
                    pointRadius : 1,
                    backgroundColor : '#FFF0',
                    data : averagePriceArray.reverse(),
                    yAxisID : 'yAxes-1'
                },
                {
                    label : '물량',
                    borderColor : 'black',
                    borderWidth: 2,
                    pointRadius : 1,
                    data : quantityArray.reverse(),
                    yAxisID : 'yAxes-2'
                }
            ]
        };

        let options = {
            scales : {
                yAxes : [
                    {
                        id : 'yAxes-1',
                        type :'linear',
                        display : true,
                        position : 'left',
                        ticks : {
                        }
                    },
                    {
                        id : 'yAxes-2',
                        type:'linear',
                        display: true,
                        position: 'right',
                        gridLines : {
                            display : false
                        }
                    }
                ]
            }
        };

        return (
            <Line
                data={ data }
                options={ options }
            />
        )
    }
}

export default LineChart;