import React from 'react';
import { Link } from "react-router-dom";
import '../styles/Header.css';
let origin = window.origin.toString();

class Header extends React.Component {

    state = {

    };

    componentDidMount () {

    }

    toggleClass = (e) => {
        e.preventDefault();
        let otherElems = document.querySelectorAll('.header__elem1__link');
        otherElems.forEach( elem => elem.classList.remove('--selected'));
        let elem = e.currentTarget.classList;
        elem.add('--selected');
    };

    render () {
        return(

            <div className={"header"}>
                <div className={"header__elem1"}>
                    <div className={"header__elem1__logo"}> Logo </div>
                    <div className={"header__elem1__link quick"} onClick={this.toggleClass}> <Link to="/quick">Quick</Link> </div>
                    <div className={"header__elem1__link search"} onClick={this.toggleClass}> <Link to="/wd">Search</Link> </div>
                    <div className={"header__elem1__link about"} onClick={this.toggleClass}> <Link to="/about">About</Link> </div>
                </div>

                <div className={"header__elem2"}>
                    <div> { origin.includes('osh') ? 'public' : 'live-dev' } </div>
                </div>
            </div>
            )
    }
}

export default Header;