/* eslint-disable no-use-before-define */
import React from 'react';
import TextField from '@material-ui/core/TextField';
import '../styles/tf.css'
import Autocomplete from '@material-ui/lab/Autocomplete';

let index = [];

class freeSolo extends React.Component {

    state = {};

    async componentDidMount () {
        console.log('🔌 component.tf.mount');
        let r = await fetch('/api/getIndex');
        let j = await r.json();
        index = [];
        await j.forEach(elem => index.push({title: elem}));
        this.setState(index);
    }

    submit = (e) => {
        let query = document.querySelector("#autoComplete").value;
        if(e.key === 'Enter' && query.length !== 0) this.props.onEnter(query)
    };


    render (){
        return (<div style={{ width: 300 }}>
            <Autocomplete
                id="autoComplete"
                options={index.map((option) => option.title)}
                renderInput={(params) => (
                    <div ref={params.InputProps.ref} className={"search-input"}>
                        <div className={"fas-wrapper"}><i className="fas fa-search"></i></div>

                        <input
                            {...params.inputProps}
                            label="freeSolo"
                            margin="normal"
                            variant="outlined"
                            placeholder={"검색할 아이템 입력"}
                            onKeyDown={this.submit}
                        />
                    </div>
                )}
            />
            {/*<Autocomplete*/}
                {/*freeSolo*/}
                {/*id="free-solo-2-demo"*/}
                {/*disableClearable*/}
                {/*options={top100Films.map((option) => option.title)}*/}
                {/*renderInput={(params) => (*/}
                    {/*<TextField*/}
                        {/*{...params}*/}
                        {/*label="Search input"*/}
                        {/*margin="normal"*/}
                        {/*variant="outlined"*/}
                        {/*InputProps={{ ...params.InputProps, type: 'search' }}*/}
                    {/*/>*/}
                {/*)}*/}
            {/*/>*/}
        </div>)
    }
}


export default freeSolo;